//高德SDK
import amap from '@/common/SDK/amap-wx.js';
export default {
	data() {
		return {
			showCategoryIndex:1005000,
			headerPosition:"fixed",
			city:"北京",
			// 分类层级
			categoryParent: [],
			//分类列表
			categoryList:[],
			// 分类层级详情
			currentCategory: []
		}
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	onLoad() {
		this.amapPlugin = new amap.AMapWX({  
			//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
			key: '7c235a9ac4e25e482614c6b8eac6fd8e'  
		});
		//定位地址
		this.amapPlugin.getRegeo({  
			success: (data) => {
				this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g,'');//把"市"去掉
			}  
		}); 
		uni.request({
			url: 'https://www.xinghuohan.cn/xhh-wx-api/wx/catalog/index',
			method: 'GET',
			data: {},
			success: res => {
				this.categoryParent = res.data.data.categoryList;
			},
			fail: () => {},
			complete: () => {
				this.showCategory(this.categoryParent[0].id);
			}
		});
	},
	methods: {
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../msg/msg'
			})
		},
		//分类切换显示
		showCategory(index){
			this.showCategoryIndex = index;
			uni.request({
				url: 'https://www.xinghuohan.cn/xhh-wx-api/wx/catalog/current?id='+index,
				method: 'GET',
				data: {},
				success: res => {
					this.categoryList = res.data.data;
					this.currentCategory = res.data.data.currentCategory;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		toCategory(e){
			//uni.showToast({title: e.name,icon:"none"});
			uni.navigateTo({
				// url: '../goods/goods-list?cid='+e.id+'&name='+e.name,
				url: '/pages/product/list?cid='+e.id
			});
		},
		//搜索跳转
		toSearch(){
			uni.showToast({title: "建议跳转到新页面做搜索功能"});
		}
	}
}
